html {
  overflow-x: hidden;
  max-width: 100vw;
  font-family: Arial, Helvetica, sans-serif;
  background: #18191a; }

body {
  max-width: 100%;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1rem;
  background: #18191a;
  color: #ffffff; }

body.overflowY, html.overflowY {
  overflow-y: hidden; }

strong {
  font-weight: bold; }

.normalize-ul {
  margin: 0;
  padding: 0;
  list-style: none; }

.no-margin {
  margin: 0; }

.container, .conainer-block {
  width: 100%;
  padding: 0 20px; }

@media (min-width: 1041px) {
  .container {
    padding: 0 calc(50% - 500px); } }

.conainer-block {
  max-width: 1040px;
  margin: 0 auto; }

.btn {
  padding: 12px 18px;
  border: none;
  font: inherit;
  font-weight: bold;
  cursor: pointer;
  border-radius: 4px; }
  .btn::-moz-focus-inner {
    border: none;
    padding: 0; }
  .btn--full {
    background: #ff6600;
    color: #ffffff;
    min-width: 130px; }
  .btn--empty {
    background: none;
    color: inherit; }

.input--switch {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 30px;
  background-color: #868686;
  border-radius: 34px;
  cursor: pointer;
  vertical-align: middle; }
  .input--switch__input {
    position: absolute;
    top: 0;
    left: 0;
    width: 0%;
    height: 0%;
    opacity: 0; }
  .input--switch__circle {
    width: 24px;
    height: 24px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #ffffff;
    position: absolute;
    top: 50%;
    left: 4px;
    transform: translate(0, -50%);
    transition: background-color 0.4s, transform 0.4s; }
    .input--switch__circle svg {
      width: 13px;
      height: auto; }
    .input--switch__circle path {
      fill: #18191a;
      transition: fill 0.4s; }
  .input--switch__input:checked + .input--switch__circle {
    background: #18191a;
    transform: translate(18px, -50%); }
    .input--switch__input:checked + .input--switch__circle path {
      fill: #fff; }

.input--lang {
  position: relative;
  display: inline-block;
  margin-right: 10px;
  margin-left: 10px;
  padding-left: 2em;
  cursor: pointer; }
  .input--lang__input {
    position: absolute;
    top: 0;
    left: 0;
    width: 0%;
    height: 0%;
    opacity: 0; }
  .input--lang__text {
    position: absolute;
    top: 50%;
    left: 0px;
    transform: translate(0, -50%); }
    .input--lang__text--en {
      display: none; }
    .input--lang__text--ru {
      display: block; }
  .input--lang__svg {
    vertical-align: top;
    transition: transform 0.3s; }
  .input--lang__input:checked ~ .input--lang__svg {
    transform: rotate(180deg); }
  .input--lang__input:checked ~ .input--lang__text--ru {
    display: none; }
  .input--lang__input:checked ~ .input--lang__text--en {
    display: block; }

.header a {
  color: #ffffff;
  text-decoration: none; }

.header__top {
  padding: 12px 15px;
  padding-bottom: 7px;
  display: flex;
  flex-direction: column;
  align-items: center; }
  @media (min-width: 1270px) {
    .header__top {
      padding: 48px 52px;
      flex-direction: row;
      justify-content: space-between;
      align-items: center; } }
  .header__top__logo {
    flex: 0 0 auto;
    width: 187px;
    margin-bottom: 10px; }
    @media (min-width: 1270px) {
      .header__top__logo {
        margin-right: 45px;
        margin-bottom: 0; } }
  .header__top__wrapper {
    max-width: 1200px;
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    width: 100%; }
    @media (min-width: 1270px) {
      .header__top__wrapper {
        flex-wrap: nowrap;
        justify-content: space-between; } }
  .header__top__nav {
    display: flex;
    align-items: center;
    font-weight: bold;
    padding: 5px 10px;
    margin: 0 auto;
    justify-content: center;
    flex-wrap: wrap; }
    @media (min-width: 1270px) {
      .header__top__nav {
        padding: 0;
        margin-left: -10px;
        margin-right: 25px; } }
    .header__top__nav a {
      padding: 10px; }
      .header__top__nav a.active {
        color: #ff6600; }
  .header__top__right {
    display: flex;
    align-items: center;
    padding: 5px 10px;
    margin: 0 auto;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 10px; }
    @media (min-width: 500px) {
