.header__bottom {
  padding-right: 52px;
  padding-left: 52px;
  border-top: 1px solid rgba(197, 190, 215, 0.2);
  border-bottom: 1px solid rgba(197, 190, 215, 0.2);
  overflow: hidden;
  display: flex; }
  @media (min-width: 1270px) {
    .header__bottom {
      display: block;
      padding-left: 269px; } }
  .header__bottom__wrapper {
    font-weight: bold;
    white-space: nowrap;
    margin-right: auto;
    margin-left: auto; }
    @media (min-width: 1270px) {
      .header__bottom__wrapper {
        max-width: 1215px;
        margin-right: 0; } }
    .header__bottom__wrapper a {
      display: inline-block;
      padding: 30px 15px;
      position: relative; }
      .header__bottom__wrapper a:before {
        content: '';
        height: 5px;
        width: calc(100% - 30px);
        position: absolute;
        bottom: 0;
        left: 15px;
