*, *:after, *:before {
  box-sizing: border-box;
  -webkit-appearance: none;
  appearance: none;
  outline: 0;
  /* for Internet Explorer */
  background: none;
}
html {
  overflow-x: hidden;
  max-width: 100vw;
  font-family: Arial, Helvetica, sans-serif;
  background: #18191a;
}
body {
  max-width: 100%;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1rem;
  background: #18191a;
  color: #ffffff;
}
body.overflowY, html.overflowY {
  overflow-y: hidden;
}
strong {
  font-weight: bold;
}
.normalize-ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.no-margin {
  margin: 0;
}
$padding: 20px;
$max-width: 1000px + ($padding * 2);
.container, .conainer-block {
  width: 100%;
  padding: 0 $padding;
}
.container {
  @media (min-width: $max-width + 1) {
    padding: 0 calc(50% - #{($max-width - $padding * 2) / 2});
  }
}
.conainer-block {
  max-width: $max-width;
  margin: 0 auto;
}