@import 'normalize';
@import 'generic/base';

.btn {
  padding: 12px 18px;
  border: none;
  font: inherit;
  font-weight: bold;
  cursor: pointer;
  border-radius: 4px;
  &::-moz-focus-inner  {
    border: none;
    padding: 0;
  }
  &--full {
    background: #ff6600;
    color: #ffffff;
    min-width: 130px;
  }
  &--empty {
    background: none;
    color: inherit;
  }
}

.input--switch {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 30px;
  background-color: #868686;
  border-radius: 34px;
  cursor: pointer;
  vertical-align: middle;
  &__input {
    position: absolute;
    top: 0;
    left: 0;
    width: 0%;
    height: 0%;
    opacity: 0;
  }
  &__circle {
    width: 24px;
    height: 24px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #ffffff;
    position: absolute;
    top: 50%;
    left: 4px;
    transform: translate(0, -50%);
    transition: background-color 0.4s, transform 0.4s;
    svg {
      width: 13px;
      height: auto;
    }
    path {
      fill: #18191a;
      transition: fill 0.4s;
    }
  }
  &__input:checked + &__circle {
    background: #18191a;
    transform: translate(18px, -50%);
    path {
      fill: #fff;
    }
  }
}

.input--lang {
  position: relative;
  display: inline-block;
  margin-right: 10px;
  margin-left: 10px;
  padding-left: 2em;
  cursor: pointer;
  &__input {
    position: absolute;
    top: 0;
    left: 0;
    width: 0%;
    height: 0%;
    opacity: 0;
  }
  &__text {
    position: absolute;
    top: 50%;
    left: 0px;
    transform: translate(0, -50%);
    &--en {
      display: none;
    }
    &--ru {
      display: block;
    }
  }
  &__svg {
    vertical-align: top;
    transition: transform 0.3s;
  }
  &__input:checked ~ &__svg {
    transform: rotate(180deg);
  }
  &__input:checked ~ &__text--ru {
    display: none;
  }
  &__input:checked ~ &__text--en {
    display: block;
  }
}

.header {
  a {
    color: #ffffff;
    text-decoration: none;
  }
  &__top {
    padding: 12px 15px;
    padding-bottom: 7px;
    display: flex;
    flex-direction: column;
    align-items: center;
    @media (min-width: 1270px) {
      padding: 48px 52px;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
    }
    &__logo {
      flex: 0 0 auto;
      width: 187px;
      margin-bottom: 10px;
      @media (min-width: 1270px) {
        margin-right: 45px;
        margin-bottom: 0;
      }
    }
    &__wrapper {
      max-width: 1200px;
      flex: 1 1 auto;
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      width: 100%;
      @media (min-width: 1270px) {
        flex-wrap: nowrap;
        justify-content: space-between;
      }
    }
    &__nav {
      display: flex;
      align-items: center;
      font-weight: bold;
      padding: 5px 10px;
      margin: 0 auto;
      justify-content: center;
      flex-wrap: wrap;
      @media (min-width: 1270px) {
        padding: 0;
        margin-left: -10px;
        margin-right: 25px;
      }
      a {
        padding: 10px;
        &.active {
          color: #ff6600;
        }
      }
    }
    &__right {
      display: flex;
      align-items: center;
      padding: 5px 10px;
      margin: 0 auto;
      flex-wrap: wrap;
      justify-content: center;
      margin-top: 10px;
      @media (min-width: 500px) {
        flex-wrap: nowrap;
        margin-top: 0;
      }
      @media (min-width: 1270px) {
        padding: 0px;
        margin: 0;
      }
      & > * {
        flex: 0 0 auto;
      }
      .input--lang {
        margin-left: 20px;
        @media (min-width: 500px) {
          margin-left: 40px;
          margin-right: 35px;
        }
      }
      .btn--empty {
        margin-right: 10px;
      }
      .breake {
        width: 100%;
        height: 15px;
        @media (min-width: 500px) {
          display: none;
        }
      }
    }
  }
  &__bottom {
    padding-right: 52px;
    padding-left: 52px;
    border-top: 1px solid rgba(197, 190, 215, 0.2);
    border-bottom: 1px solid rgba(197, 190, 215, 0.2);
    overflow: hidden;
    display: flex; // ЧТобы прижимался к краю
    @media (min-width: 1270px) {
      display: block;
      padding-left: (187px + 52px + 30px);
    }
    &__wrapper {
      font-weight: bold;
      white-space: nowrap;
      margin-right: auto;
      margin-left: auto;
      @media (min-width: 1270px)  {
        max-width: 1215px;
        margin-right: 0;
      }
      a {
        display: inline-block;
        padding: 30px 15px;
        position: relative;
        &:before {
          content: '';
          height: 5px;
          width: calc(100% - 30px);
          position: absolute;
          bottom: 0;
          left: 15px;
          display: block;
          transition: background-color 0.3s;
        }
        &.active:before {
          background: #ff6600;
        }
      }
    }
  }
}